import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { fetchLoggedInUserOrdersAsync } from '../userSlice'
import { discountedPrice } from '../../../app/constant';
import { DNA } from 'react-loader-spinner';

const UserOrders = () => {
    const dispatch = useDispatch();

    const userOrders = useSelector((state) => state.user.userInfo.orders)
    const userInfoStatus = useSelector((state) => state.user?.status);

    useEffect(()=> {
        dispatch(fetchLoggedInUserOrdersAsync())
    }, [dispatch])

  return (
      <div className='mx-auto max-w-4xl px-4 sm:px-6 lg:px-8'>
          <h1 className="text-2xl mb-5 mt-4 font-bold tracking-tight text-gray-900">My orders:</h1>
          { userOrders && userOrders?.map((order)=> (
                  <div className=" bg-white mt-8">

                      <div className="border-t border-gray-200 px-4 py-6 sm:px-6">
                          <div className="flow-root">
                              <ul className="-my-6 divide-y divide-gray-200">
                                  {order?.items?.map((item) => (
                                      <li key={item.id} className="flex py-6">
                                          <div className="h-24 w-24 flex-shrink-0 overflow-hidden rounded-md border border-gray-200">
                                              <img
                                                  src={item.product.thumbnail}
                                                  alt={item.product.title}
                                                  className="h-full w-full object-cover object-center"
                                              />
                                          </div>

                                          <div className="ml-4 flex flex-1 flex-col">
                                              <div>
                                                  <div className="flex justify-between text-base font-medium text-gray-900">
                                                      <h3>
                                                          <a href={item.product.href}>{item.product.title}</a>
                                                      </h3>
                                                      <p className="ml-4">${discountedPrice(item.product)}</p>
                                                  </div>
                                                  <p className="mt-1 text-sm text-gray-500">{item.product.brand}</p>
                                              </div>
                                              <div className="flex flex-1 items-end justify-between text-sm">
                                                  <div className="text-gray-500 flex items-center gap-1">Qty : {item?.quantity}
                                                  </div>
                                              </div>
                                          </div>
                                      </li>
                                  ))}
                              </ul>
                          </div>
                      </div>

                      <div className="border-t border-gray-200 px-4 py-6 sm:px-6">
                          <div className="flex justify-between text-base font-medium text-gray-900">
                              <p>Subtotal</p>
                              <p>$ {order?.totalAmount}</p>
                          </div>
                          <div className="flex justify-between text-base font-medium text-gray-900">
                              <p>Total items in cart</p>
                              <p>{order?.totalItems} items</p>
                          </div>
                      <p className='text-sm text-[#777]'>Order Status: <span className='text-sm text-[#333] font-semibold'>{order?.status}</span></p>
                      </div>
                  </div>
          ))}

          {
              userInfoStatus === 'loading' ?
                  <DNA
                      visible={true}
                      height="80"
                      width="80"
                      ariaLabel="dna-loading"
                      wrapperStyle={{}}
                      wrapperClass="dna-wrapper"
                  /> : null
          }
    </div>
  )
}

export default UserOrders