import React, { useEffect, useState } from 'react'
import { PhotoIcon, UserCircleIcon } from '@heroicons/react/24/solid'
import { useDispatch, useSelector } from 'react-redux';
import { useForm } from 'react-hook-form';
import { clearSelectedProduct, createProductAsync, createProductsAsync, fetchProductByIdAsync, updateProductAsync } from '../../product/productSlice';
import { useParams } from 'react-router-dom';
import Modal from '../../common/Modal';
import { useAlert } from 'react-alert';

const ProductForm = () => {
    const dispatch = useDispatch()
    const selectedProduct = useSelector((state) => state.product.selectedProduct);
    console.log(selectedProduct, "# selectedProduct");
    const [openModal, setOpenModal] = useState(null);
    const alert = useAlert();

    const {
        register,
        handleSubmit,
        formState: { errors },
        setValue,
        reset
    } = useForm();

    const params = useParams();

    // to edit product: idea is: using the product id, will fetch that product and fill the form with the product information
    useEffect(() => {
        if (params.id) {
            dispatch(fetchProductByIdAsync(params.id));
        } else {
            dispatch(clearSelectedProduct())
        }
    }, [params.id, dispatch])

    useEffect(() => {
        if (selectedProduct && params.id) {
            setValue("title", selectedProduct.title)
            setValue("description", selectedProduct.description)
            setValue("price", selectedProduct.price)
            setValue("discountPercentage", selectedProduct.discountPercentage)
            setValue("thumbnail", selectedProduct.thumbnail)
            setValue("stock", selectedProduct.stock)
            setValue("images", selectedProduct.images)
            setValue("brand", selectedProduct.brand)
            setValue("category", selectedProduct.category)
            setValue("image1", selectedProduct.images[0])
            setValue("image2", selectedProduct.images[1])
            setValue("image3", selectedProduct.images[2])

        }
    }, [selectedProduct, params.id, setValue])

    const brands = useSelector((state) => state.product.brands);
    const categories = useSelector((state) => state.product.categories);

    const handleHideProduct = () => {
        // here to delete a product we are not actually deleting, but setting a property named deleted: true. main purpose behind this is to keep information of the old product
        const product = { ...selectedProduct };
        product.hidden = true;
        dispatch(updateProductAsync(product));
    }

    return (
        <>
            <form
                noValidate
                onSubmit={handleSubmit((data) => {
                    // need to mutate, because we want array of images 
                    const product = { ...data }
                    product.images = [product.image1, product.image2, product.image3, product.thumbnail];
                    delete product['image1']
                    delete product['image2']
                    delete product['image3']
                    product.rating = 0;

                    product.price = +product.price;
                    product.stock = +product.stock;
                    product.discountPercentage = +product.discountPercentage;
                    console.log(product);
                    if (params.id) {
                        product.id = params.id;
                        product.rating = selectedProduct.rating || 0;
                        dispatch(updateProductAsync(product));
                        alert.success("Product updated successfully")
                        reset();
                    } else {
                        dispatch(createProductAsync(product));
                        reset();
                        alert.success("Product created successfully")
                    }
                })}>
                <div className="space-y-12 bg-white p-4 rounded-md">
                    <div className="border-b border-gray-900/10 pb-12">
                        <div className="mt-10 grid grid-cols-1 gap-x-6 gap-y-8 sm:grid-cols-6">
                            {/* title  */}
                            <div className="sm:col-span-4">
                                <label htmlFor="title" className="block text-sm font-medium leading-6 text-gray-900">
                                    Product title
                                </label>
                                <div className="mt-2">
                                    <div className="flex rounded-md shadow-sm ring-1 ring-inset ring-gray-300 focus-within:ring-2 focus-within:ring-inset focus-within:ring-indigo-600">
                                        <input
                                            type="text"
                                            {...register("title", {
                                                required: "Please enter product title"
                                            })}
                                            id="title"
                                            className="block flex-1 border-0 bg-transparent py-1.5 pl-1 text-gray-900 placeholder:text-gray-400 focus:ring-0 sm:text-sm sm:leading-6 w-full"
                                            placeholder="Enter product name"
                                        />
                                    </div>
                                    {errors.title && <p className='text-red-500'>{errors.title.message}</p>}
                                </div>
                            </div>

                            {/* description  */}
                            <div className="col-span-full">
                                <label htmlFor="description" className="block text-sm font-medium leading-6 text-gray-900">
                                    Description
                                </label>
                                <div className="mt-2">
                                    <textarea
                                        id="description"
                                        {...register('description', { required: "Please enter product description" })}
                                        rows={3}
                                        className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                                    />
                                </div>
                                {errors.description && <p className='text-red-500'>{errors.description.message}</p>}
                            </div>

                            {/* brands  */}
                            <div className="col-span-3">
                                <label htmlFor="brand" className="block text-sm font-medium leading-6 text-gray-900">
                                    Brands
                                </label>
                                <div className="mt-2">
                                    {/* TODO: need to implement search functionality in select  */}
                                    <select className=' rounded-md' {...register("brand", { required: "select brand" })}>
                                        <option value={""}>--choose brands--</option>
                                        {brands.map((brand) => (
                                            <option key={brand.value} value={brand.value}>{brand.label}</option>
                                        ))}
                                    </select>
                                </div>
                                {errors.brand && <p className='text-red-600'>{errors.brand.message}</p>}
                            </div>

                            {/* categories  */}
                            <div className="col-span-3">
                                <label htmlFor="category" className="block text-sm font-medium leading-6 text-gray-900">
                                    Categories
                                </label>
                                <div className="mt-2">
                                    {/* TODO: need to implement search functionality in select  */}
                                    <select className=' rounded-md' {...register("category", {
                                        required: "Select category"
                                    })}>
                                        <option value={""}>--choose categories--</option>
                                        {categories.map((category) => (
                                            <option key={category.value} value={category.value}>{category.label}</option>
                                        ))}
                                    </select>
                                </div>
                                {errors.category && <p className='text-red-600'>{errors.category.message}</p>}
                            </div>

                            {/* price  */}
                            <div className="sm:col-span-2">
                                <label htmlFor="price" className="block text-sm font-medium leading-6 text-gray-900">
                                    Price
                                </label>
                                <div className="mt-2">
                                    <div className="flex rounded-md shadow-sm ring-1 ring-inset ring-gray-300 focus-within:ring-2 focus-within:ring-inset focus-within:ring-indigo-600 w-full">
                                        <input
                                            type="number"
                                            {...register("price", {
                                                required: "Enter product price",
                                                min: 1
                                            })}
                                            id="price"
                                            className="block flex-1 border-0 bg-transparent py-1.5 pl-1 text-gray-900 placeholder:text-gray-400 focus:ring-0 sm:text-sm sm:leading-6 w-full"
                                            placeholder="Set product price"
                                        />
                                    </div>
                                </div>
                                {errors.price && <p className='text-red-600'>{errors.price.message}</p>}
                            </div>

                            {/* discount  */}
                            <div className="sm:col-span-2">
                                <label htmlFor="discountPercentage" className="block text-sm font-medium leading-6 text-gray-900">
                                    Discount
                                </label>
                                <div className="mt-2">
                                    <div className="flex rounded-md shadow-sm ring-1 ring-inset ring-gray-300 focus-within:ring-2 focus-within:ring-inset focus-within:ring-indigo-600 w-full">
                                        <input
                                            type="number"
                                            {...register("discountPercentage", {
                                                required: "Enter product discount",
                                                min: 1,
                                                max: 100
                                            })}
                                            id="discount"
                                            className="block flex-1 border-0 bg-transparent py-1.5 pl-1 text-gray-900 placeholder:text-gray-400 focus:ring-0 sm:text-sm sm:leading-6 w-full"
                                            placeholder="Set product price discount %"
                                        />
                                    </div>
                                </div>
                                {errors.discountPercentage && <p className='text-red-600'>{errors.discountPercentage.message}</p>}
                            </div>

                            {/* stock  */}
                            <div className="sm:col-span-2">
                                <label htmlFor="stock" className="block text-sm font-medium leading-6 text-gray-900">
                                    Product in Stock
                                </label>
                                <div className="mt-2">
                                    <div className="flex rounded-md shadow-sm ring-1 ring-inset ring-gray-300 focus-within:ring-2 focus-within:ring-inset focus-within:ring-indigo-600 w-full">
                                        <input
                                            type="number"
                                            {...register("stock", {
                                                required: "Enter product stock",
                                                min: 0,
                                            })}
                                            id="stock"
                                            className="block flex-1 border-0 bg-transparent py-1.5 pl-1 text-gray-900 placeholder:text-gray-400 focus:ring-0 sm:text-sm sm:leading-6 w-full"
                                            placeholder="How much stock"
                                        />
                                    </div>
                                </div>
                                {errors.stock && <p className='text-red-600'>{errors.stock.message}</p>}
                            </div>

                            {/* TODO: implement image upload later  */}


                            {/* thumbnail  */}
                            <div className="sm:col-span-6">
                                <label htmlFor="thumbnail" className="block text-sm font-medium leading-6 text-gray-900">
                                    Product thumbnail
                                </label>
                                <div className="mt-2">
                                    <div className="flex rounded-md shadow-sm ring-1 ring-inset ring-gray-300 focus-within:ring-2 focus-within:ring-inset focus-within:ring-indigo-600">
                                        <input
                                            type="text"
                                            {...register("thumbnail", {
                                                required: "Enter product thumbnail"
                                            })}
                                            id="thumbnail"
                                            className="block flex-1 border-0 bg-transparent py-1.5 pl-1 text-gray-900 placeholder:text-gray-400 focus:ring-0 sm:text-sm sm:leading-6 w-full"
                                            placeholder="Enter product thumbnail"
                                        />
                                    </div>
                                </div>
                                {errors.thumbnail && <p className='text-red-600'>{errors.thumbnail.message}</p>}
                            </div>

                            {/* img1  */}
                            <div className="sm:col-span-6">
                                <label htmlFor="image1" className="block text-sm font-medium leading-6 text-gray-900">
                                    Product image 1
                                </label>
                                <div className="mt-2">
                                    <div className="flex rounded-md shadow-sm ring-1 ring-inset ring-gray-300 focus-within:ring-2 focus-within:ring-inset focus-within:ring-indigo-600">
                                        <input
                                            type="text"
                                            {...register("image1", {
                                                required: "Enter product image1"
                                            })}
                                            id="image1"
                                            className="block flex-1 border-0 bg-transparent py-1.5 pl-1 text-gray-900 placeholder:text-gray-400 focus:ring-0 sm:text-sm sm:leading-6 w-full"
                                            placeholder="Enter product image 1"
                                        />
                                    </div>
                                </div>
                                {errors.image1 && <p className='text-red-600'>{errors.image1.message}</p>}
                            </div>
                            {/* img2  */}
                            <div className="sm:col-span-6">
                                <label htmlFor="image2" className="block text-sm font-medium leading-6 text-gray-900">
                                    Product image 2
                                </label>
                                <div className="mt-2">
                                    <div className="flex rounded-md shadow-sm ring-1 ring-inset ring-gray-300 focus-within:ring-2 focus-within:ring-inset focus-within:ring-indigo-600">
                                        <input
                                            type="text"
                                            {...register("image2", {
                                                required: "Enter product image2"
                                            })}
                                            id="image2"
                                            className="block flex-1 border-0 bg-transparent py-1.5 pl-1 text-gray-900 placeholder:text-gray-400 focus:ring-0 sm:text-sm sm:leading-6 w-full"
                                            placeholder="Enter product image 2"
                                        />
                                    </div>
                                </div>
                                {errors.image2 && <p className='text-red-600'>{errors.image2.message}</p>}
                            </div>
                            {/* img3 */}
                            <div className="sm:col-span-6">
                                <label htmlFor="image3" className="block text-sm font-medium leading-6 text-gray-900">
                                    Image 3
                                </label>
                                <div className="mt-2">
                                    <div className="flex rounded-md shadow-sm ring-1 ring-inset ring-gray-300 focus-within:ring-2 focus-within:ring-inset focus-within:ring-indigo-600">
                                        <input
                                            type="text"
                                            {...register("image3", {
                                                required: "Enter product image3"
                                            })}
                                            id="image3"
                                            className="block flex-1 border-0 bg-transparent py-1.5 pl-1 text-gray-900 placeholder:text-gray-400 focus:ring-0 sm:text-sm sm:leading-6 w-full"
                                            placeholder="Enter product image 3"
                                        />
                                    </div>
                                </div>
                                {errors.image3 && <p className='text-red-600'>{errors.image3.message}</p>}
                            </div>
                        </div>
                    </div>
                </div>

                <div className="mt-6 flex items-center justify-end gap-x-6">
                    <button type="button" className="text-sm font-semibold leading-6 text-gray-900">
                        Cancel
                    </button>

                    <button
                        type="submit"
                        className="rounded-md bg-indigo-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
                    >
                        Save
                    </button>
                    {selectedProduct && !selectedProduct.hidden && <button
                        type="submit"
                        className="rounded-md bg-yellow-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-yellow-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-yellow-600"
                        onClick={(e) => { e.preventDefault(); setOpenModal(true) }}
                    >
                        Hide Product
                    </button>}
                </div>
            </form>

           { selectedProduct && <Modal
                title={`Hide ${selectedProduct.title}`}
                message="Are you sure you want to hide this Product from customer?"
                dangerOption="Hide Product"
                cancelOption="Cancel"
                dangerAction={handleHideProduct}
                cancelAction={() => setOpenModal(null)}
                showModal={openModal}
            ></Modal>}
        </>
    )
}

export default ProductForm