
export function addToCart(item) {
  return new Promise(async (resolve) => {
    const response = await fetch("/cart", {
      method: 'POST',
      body: JSON.stringify(item),
      headers: {
        'content-type': 'application/json'
      }
    });
    const data = await response.json();
    resolve({ data })
  }
  );
}

export function fetchItemsByUserId() {
  return new Promise(async (resolve) => {
    const response = await fetch("/cart");
    const data = await response.json();
    resolve({ data })
  }
  );
}

export function updateCart(updateItem) {
  return new Promise(async (resolve) => {
    const response = await fetch("/cart/"+updateItem.id, {
      method: 'PATCH',
      body: JSON.stringify(updateItem),
      headers: {
        'content-type': 'application/json'
      }
    });
    const data = await response.json();
    resolve({ data })
  }
  );
}

export function deleteItemFromCart(itemId) {
  return new Promise(async (resolve) => {
    const response = await fetch("/cart/"+itemId, {
      method: 'DELETE',
      headers: {
        'content-type': 'application/json'
      }
    });
    const data = await response.json();
    resolve({ data: {id: itemId} })
  }
  );
}

// after placing order successfully, need to remove item from cart
export function resetCart() {
  return new Promise(async (resolve) => {
    // at first get all the items of the perticular user
    const response = await fetchItemsByUserId();
    const items = response.data;
    
    // then delete those items
    for (let item of items) {
      await deleteItemFromCart(item?.id)
    }
    resolve({status: "success"})
  })
}