
import { useEffect, useState } from 'react'
import { StarIcon } from '@heroicons/react/20/solid'
import { RadioGroup } from '@headlessui/react'
import { useDispatch, useSelector } from 'react-redux'
import { fetchProductByIdAsync } from '../productSlice'
import { useParams } from 'react-router-dom'
import { addToCartAsync } from '../../cart/CartSlice'
import { discountedPrice } from '../../../app/constant'
import {useAlert} from "react-alert";
import ReactImageGallery from 'react-image-gallery'

const colors = [
    { name: 'White', class: 'bg-white', selectedClass: 'ring-gray-400' },
    { name: 'Gray', class: 'bg-gray-200', selectedClass: 'ring-gray-400' },
    { name: 'Black', class: 'bg-gray-900', selectedClass: 'ring-gray-900' },
]
const sizes = [
    { name: 'XXS', inStock: false },
    { name: 'XS', inStock: true },
    { name: 'S', inStock: true },
    { name: 'M', inStock: true },
    { name: 'L', inStock: true },
    { name: 'XL', inStock: true },
    { name: '2XL', inStock: true },
    { name: '3XL', inStock: true },
]

function classNames(...classes) {
    return classes.filter(Boolean).join(' ')
}

export default function ProductDetails() {
    const [selectedColor, setSelectedColor] = useState(colors[0])
    const [selectedSize, setSelectedSize] = useState(sizes[2])
    const dispatch = useDispatch();
    const params = useParams()
    const product = useSelector((state) => state.product.selectedProduct);
    const user = useSelector((state) => state.auth.loggedInUser);
    const cartItems = useSelector(state => state.cart.items)
    const alert = useAlert();
    const [selectedImgIndex, setSelectedImgIndex] = useState(0)

    useEffect(() => {
        dispatch(fetchProductByIdAsync(params.id))
    }, [dispatch, params.id])

    const handleCart = (e) => {
        e.preventDefault();
        if (cartItems?.findIndex((item) => item?.product?.id === product?.id) < 0) {
            const newItem = {
                product: product.id,
                quantity: 1
            }
            dispatch(addToCartAsync(newItem));
            alert.success("Product added to cart successfully")
        } else {
            alert.info("product already added");
        }
    }

    console.log(product, "# product");
    const images = [
        {
            original: product?.images[0],
            thumbnail: product?.images[0],
        },
        {
            original: product?.images[1],
            thumbnail: product?.images[1],
        },
        {
            original: product?.images[2],
            thumbnail: product?.images[2],
        },
    ];

    return (
        <div className="bg-white">
            {product ? 
            <div className="pt-6 w-full p-4">
              

                <div className='grid grid-cols-1 md:grid-cols-2 gap-5 md:gap-8'
                // className="mx-auto mt-6 max-w-2xl sm:px-6 lg:grid lg:max-w-[90rem] lg:grid-cols-3 lg:gap-x-8 lg:px-8"
                >
                    
                        {/* Image gallery */}
                        <div 
                        // className="aspect-h-4 aspect-w-3 hidden overflow-hidden rounded-lg lg:block"
                            className='flex flex-col gap-5'
                        >
                            <img 
                            src={product.images[selectedImgIndex]}
                            alt=''
                            className='w-full h-96 object-cover rounded-md'
                            />
                            <div className='flex gap-5'>
                                {
                                    product.images.map((image, index) => {
                                        return (
                                            <img
                                            className={'w-10 h-8 md:w-14 md:h-14 object-cover rounded-md cursor-pointer ' + (selectedImgIndex === index ? "border-2 border-blue-800 border-solid p-1" : "")}
                                            alt=''
                                            src={image}
                                            onClick={()=>setSelectedImgIndex(index)}
                                            />
                                        )
                                    })
                                }
                            </div>
                        </div>

                        {/* Product info */}
                        <div 
                        // className="mx-auto max-w-2xl px-4 pb-16 pt-10 sm:px-6 lg:grid lg:max-w-[90rem] lg:grid-cols-3 lg:grid-rows-[auto,auto,1fr] lg:gap-x-8 lg:px-8 lg:pb-24 lg:pt-16"
                            className=''
                        >
                            {/* <div className="lg:col-span-2 lg:border-r lg:border-gray-200 lg:pr-8"> */}
                                <h1 className="text-2xl font-bold tracking-tight text-gray-900 sm:text-3xl mb-2">{product.title}</h1>
                            {/* </div> */}

                            {/* Options */}
                            <div className="mt-4 lg:row-span-3 lg:mt-0">
                                {/* <h2 className="">Product information</h2> */}
                                <div className='flex gap-2 items-center'>
                                    <p className="text-2xl md:text-3xl tracking-tight text-gray-900">₹{discountedPrice(product)}</p>
                                    <p className="text-xl md:text-2xl tracking-tight text-gray-400 line-through"><span className='text-[#666]'>₹</span>{product.price}</p>
                                    <p className='text-base md:text-xl text-[#38908E] font-medium'>{product?.discountPercentage}% off</p>
                                    
                                </div>


                                {/* Reviews */}
                                {/* <div className="mt-6">
                                    <h3 className="">Reviews</h3>
                                    <div className="flex items-center">
                                        <div className="flex items-center">
                                            {[0, 1, 2, 3, 4].map((rating) => (
                                                <StarIcon
                                                    key={rating}
                                                    className={classNames(
                                                        product.rating > rating ? 'text-gray-900' : 'text-gray-200',
                                                        'h-5 w-5 flex-shrink-0'
                                                    )}
                                                    aria-hidden="true"
                                                />
                                            ))}
                                        </div>
                                        <p className="">{product.rating} out of 5 stars</p>

                                    </div>
                                </div> */}

                                <div className=" lg:col-span-2 lg:col-start-1 lg:border-r lg:border-gray-200 lg:pb-16 lg:pr-8 lg:pt-6">
                                    {/* <div>
                                        <h3 className="">Description</h3>

                                        <div className="space-y-6">
                                            <p className="text-base text-gray-900">{product.description}</p>
                                        </div>
                                    </div> */}

                                    {/* <div className="mt-10">
                                        <h3 className="text-sm font-medium text-gray-900">Highlights</h3>

                                        <div className="mt-4">
                                            <ul className="list-disc space-y-2 pl-4 text-sm">
                                                {product.highlights && product.highlights.map((highlight) => (
                                                    <li key={highlight} className="text-gray-400">
                                                        <span className="text-gray-600">{highlight}</span>
                                                    </li>
                                                ))}
                                            </ul>
                                        </div>
                                    </div> */}

                                    <div className="mt-8 md:mt-10">
                                        <h2 className="text-sm font-medium text-gray-900">Details</h2>

                                        <div className="mt-2 md:mt-4 space-y-6">
                                            <p className="text-sm text-gray-600 font-sans">{product.description}</p>
                                        </div>
                                    </div>
                                </div>

                                <form className="mt-10 w-full items-center">
                                    {/* Colors */}
                                    {/* <div>
                                        <h3 className="text-sm font-medium text-gray-900">Color</h3>

                                        <RadioGroup value={selectedColor} onChange={setSelectedColor} className="mt-4">
                                            <RadioGroup.Label className="sr-only">Choose a color</RadioGroup.Label>
                                            <div className="flex items-center space-x-3">
                                                {colors && colors.map((color) => (
                                                    <RadioGroup.Option
                                                        key={color.name}
                                                        value={color}
                                                        className={({ active, checked }) =>
                                                            classNames(
                                                                color.selectedClass,
                                                                active && checked ? 'ring ring-offset-1' : '',
                                                                !active && checked ? 'ring-2' : '',
                                                                'relative -m-0.5 flex cursor-pointer items-center justify-center rounded-full p-0.5 focus:outline-none'
                                                            )
                                                        }
                                                    >
                                                        <RadioGroup.Label as="span" className="sr-only">
                                                            {color.name}
                                                        </RadioGroup.Label>
                                                        <span
                                                            aria-hidden="true"
                                                            className={classNames(
                                                                color.class,
                                                                'h-8 w-8 rounded-full border border-black border-opacity-10'
                                                            )}
                                                        />
                                                    </RadioGroup.Option>
                                                ))}
                                            </div>
                                        </RadioGroup>
                                    </div> */}

                                    

                                    <button
                                        type="submit"
                                        className="mt-10 flex w-full items-center justify-center rounded-md border border-transparent bg-indigo-600 px-8 py-3 text-base font-medium text-white hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:ring-offset-2"
                                        onClick={handleCart}
                                    >
                                        Add to cart
                                    </button>
                                </form>
                            </div>
                        </div>
                </div>
            </div> : null}
        </div>
    )
}
