
export function fetchProductById(id) {
  return new Promise(async (resolve) => {
    const response = await fetch("/products/"+id);
    const data = await response.json();
    resolve({ data })
  }
  );
}

export function fetchProductsByFilter(filterObj, sort, pagination, admin) {

  // filter = {"category" : ["smartphone", "laptop"]}
  // sort = {_sort : "price", _order=  "descending"}
  // pagination = {_page: 1, _limit=10}
  
  //TODO: server will filter hidden products

  let queryString = "";
  for(let key in filterObj) {
    const categoryValues = filterObj[key];
    if(categoryValues.length > 0){
      const lastCategoryValue = categoryValues[categoryValues.length - 1];
      queryString += `${key}=${lastCategoryValue}&`;
    }
  }

  for(let key in sort) {
    queryString += `${key}=${sort[key]}&`;
  }

  for (let key in pagination) {
    queryString += `${key}=${pagination[key]}&`;
  }
  if(admin){
    queryString +=  `admin=true`
  }

  return new Promise(async (resolve) => {
    const response = await fetch("/products?" + queryString+'isAdmin=true');
    const data = await response.json();
    const totalItems = await response.headers.get("X-Total-Count");
    resolve({ data: {products: data, totalItems: +totalItems} })
  }
  );
}


export function fetchCategories() {
  return new Promise(async (resolve) => {
    const response = await fetch("/categories");
    const data = await response.json();
    resolve({ data })
  }
  );
}


